<template>
  <div>
    <h2>评论管理</h2>
    <!-- <a-row type="flex" style="margin-bottom: 20px;">
      <a-col style="width: 100%; display: flex;">
        <a-input v-model="searchParams.keyword" style="max-width: 200px; margin-right: 12px;" />
        <a-button type="primary">搜索</a-button>
      </a-col>
    </a-row> -->
    <s-table
      ref="table"
      bordered
      size="default"
      row-key="id"
      :columns="columns"
      :data="loadData"
    >
      <template slot="product" slot-scope="text">
        {{text.product.name}}
      </template>
      <template slot="action" slot-scope="text">
        <a-button type="link" @click="toDelete(text.id)">删除</a-button>
      </template>
    </s-table>
  </div>
</template>
<script>
import STable from '@/components/Table'
import { mapState, mapActions } from 'vuex'
const getDefaultSearchParams = () => ({
  keyword: ''
})
export default {
  components: { STable },
  data() {
    const columns = [
      {
        title: 'id',
        dataIndex: 'id'
      },
      {
        title: '产品名称',
        scopedSlots: { customRender: "product"},
      },
      {
        title: '内容',
        dataIndex: 'content'
      },
      {
        title: '评分',
        dataIndex: 'score'
      },
      {
        title: '操作',
        scopedSlots: { customRender: "action"},
      }
    ]
    return {
      columns,
      searchParams: getDefaultSearchParams(),
      loadData: paramters => {
        return this.getCommentList(
          Object.assign({}, paramters, {shop_id: sessionStorage.getItem('shop_id') ? Number(sessionStorage.getItem('shop_id')): undefined})
        )
      }
    }
  },
  methods: {
    ...mapActions('comment', {
      getCommentList: 'getList',
      deleteComment: 'delete'
    }),
    toDelete(id) {
      const _this = this
      this.$confirm({
        content: '你确定要删除这个评论？',
        okText: '确认',
        cancelText: '取消',
        onOk() {
          _this.deleteComment(id).then(res => {
            _this.$refs.table.refresh(true)
          })
        }
      });
    }
  }
}
</script>